import {Link, NavLink} from "react-router-dom";
import s from './breadcrumbs.module.css'
import useBreadcrumbs from 'use-react-router-breadcrumbs';

const Breadcrumbs = (props) => {

    const routes = [
        {path: '/', breadcrumb: ''},
        {path: '/categories', breadcrumb: ''},
        {path: '/brands', breadcrumb: ''},
        {path: '/info', breadcrumb: ' '},
        {path: '/delivery', breadcrumb: ''},
        {path: '/security', breadcrumb: ' '},
        {path: '/news', breadcrumb: ''},
        {path: '/news/:new', breadcrumb: props.title},
        {path: '/account', breadcrumb: ' '},
        {path: '/basket', breadcrumb: ''},
        {path: '/contacts', breadcrumb: '  '},
        {path: '/basket/order-checkout', breadcrumb: ' '},
        {path: '/basket/success', breadcrumb: '  '},
        {path: '/account/login', breadcrumb: ''},
        {path: '/account/registration', breadcrumb: ''},
        {path: '/account/reset-password', breadcrumb: ' ?'},
        {path: '/account/address', breadcrumb: ''},
        {path: '/account/edit-profile', breadcrumb: ' '},
        {path: '/account/orders', breadcrumb: ' '},
        {path: '/account/liked', breadcrumb: ''},
        {path: '/account/change-password', breadcrumb: ' '},
        {path: 'categories/:category', breadcrumb: props.title},
        {path: 'categories/:category/:id', breadcrumb: props.name},
    ]


    const breadcrumbs = useBreadcrumbs(routes);

    return (
        <div className={s.breadcrumbs}>
            {breadcrumbs.map(({
                                  match,
                                  breadcrumb
                              }, index) => (
                <span key={match.pathname}>
          <NavLink to={match.pathname}>{breadcrumb}{index == breadcrumbs.length - 1 ? null : "/"}</NavLink>
        </span>
            ))}

        </div>
    )

}

export default Breadcrumbs;