import {useSelector} from "react-redux";
import {Navigate, NavLink} from "react-router-dom";
import React from "react";
import s from './Account.module.css';




const AccountMain = () => {
    const isAuth = useSelector(state => state.authUser.isAuth);
    const email = useSelector(state => state.authUser.email);
    const userInfo = useSelector(state => state.setUserData);


    return (<>
            {isAuth ? <div className={s.accountProfile}>
                <h3> </h3>
                <div>
                    <h5></h5>
                    <ul>
                        <li><strong>:</strong>{email}</li>
                        <li><strong>:</strong>{`${userInfo.name} ${userInfo.surname}`}</li>
                        <li><strong> :</strong>{userInfo.phone}</li>
                        <li><strong>:</strong>{userInfo.country}</li>
                    </ul>
                </div>
                <div>
                    <h5>  </h5>
                    <ul>
                        <li><NavLink to="edit-profile">  </NavLink></li>
                        <li><NavLink to="change-password">  </NavLink></li>
                        <li><NavLink to="edit-adress"> </NavLink></li>
                    </ul>
                </div>
                <div>
                    <h5> </h5>
                    <ul>
                        <li><NavLink to=""> </NavLink></li>
                        <li><NavLink to=""></NavLink></li>
                    </ul>
                </div>
            </div> : <Navigate to="login"/>}

        </>
    )
}

export default AccountMain;