const CharacterList=()=>{
    return(
        <ul>
            <li><h5>:<span>-</span></h5></li>
            <li><h5>:<span>135 /2</span></h5></li>
            <li><h5>:<span>, , , </span></h5></li>
            <li><h5> : <span>100% </span></h5></li>
            <li><h5> :<span></span></h5></li>
        </ul>
    )
}

export default CharacterList;