import Breadcrumbs from "../../assets/Components/breadcrumbs/breadcrumbs";
import {NavLink, Route, useLocation} from "react-router-dom";
import s from './Categories.module.css';
import s2 from './ActiveStyles.css';
import CategorySection from "./CategorySection";
import {Routes} from "react-router";
import React, {useState} from "react";
import ProductsInCategory from "./products/ProductsInCategory";
import navicon from "../../img/categories/navicon.svg";
import {BiazRenforse, RandomData} from "../../Data/CategoriesData";
import useDocumentTitle from "../../assets/Tools/DocumentTitle/DocumentTitle";


const Categories = () => {
    //. 
    const [ToggleMobileNav, setToggleMobileNav] = useState(true);
    const openMobileNav = () => {
        setToggleMobileNav(!ToggleMobileNav);
    }
    //   title
    const [ActiveTitle, SetActiveTitle] = useState();

    const getTitle = (title) => {
        SetActiveTitle(title);
    }


    useDocumentTitle('');

    return (
        <div className={s.categories}>
            <div className={`${s.categoriesContent} content`}>
                <Breadcrumbs title={ActiveTitle}/>
                <div className={s.contentMain}>
                    <aside className={s.categoriesSidebar}>
                        <nav className={s.categoriesMenu}>
                            <h3> <span onClick={openMobileNav}><img src={navicon} alt=""/></span></h3>
                            <ul className={`${s.categoriesList} categories__active ${ToggleMobileNav ? 'categories__mobilenav' : null}`}>
                                <li><NavLink to='linens'> </NavLink></li>
                                <li><NavLink to='biaz-renfors'>-</NavLink></li>
                                <li><NavLink to='satin-gakkard'>-</NavLink></li>
                                <li><NavLink to='satin-lux'>-</NavLink></li>
                                <li><NavLink to='satin-tvil'>-</NavLink></li>
                                <li><NavLink to='blankets'></NavLink></li>
                                <li><NavLink to='pillows'></NavLink></li>
                                <li><NavLink to='plaid'></NavLink></li>
                                <li><NavLink to='terry'> </NavLink></li>
                                <li><NavLink to='for-childrens'> </NavLink></li>
                                <li><NavLink to='for-kitchen'> </NavLink></li>
                                <li><NavLink to='sheets'></NavLink></li>
                                <li><NavLink to='bedspreads'></NavLink></li>
                                <li><NavLink to='novelties'></NavLink></li>
                            </ul>
                        </nav>
                    </aside>
                    <Routes>
                        <Route path="" element={<h3> </h3>}/>
                        <Route path="linens"
                               element={<CategorySection getTitle={getTitle} title={" "}/>}/>
                        <Route path="biaz-renfors"
                               element={<ProductsInCategory getTitle={getTitle} ProductsData={BiazRenforse}
                                                            title={"-"}/>}/>
                        <Route path="satin-gakkard"
                               element={<ProductsInCategory getTitle={getTitle} ProductsData={RandomData}
                                                            title={"-"}/>}/>
                        <Route path="satin-lux"
                               element={<ProductsInCategory getTitle={getTitle} ProductsData={RandomData}
                                                            title={"-"}/>}/>
                        <Route path="satin-tvil"
                               element={<ProductsInCategory getTitle={getTitle} ProductsData={RandomData}
                                                            title={"-"}/>}/>
                        <Route path="blankets"
                               element={<ProductsInCategory getTitle={getTitle} ProductsData={RandomData}
                                                            title={""}/>}/>
                        <Route path="pillows" element={<ProductsInCategory getTitle={getTitle} ProductsData={RandomData}
                                                                           title={""}/>}/>
                        <Route path="plaid" element={<ProductsInCategory getTitle={getTitle} ProductsData={RandomData}
                                                                         title={""}/>}/>
                        <Route path="terry"
                               element={<ProductsInCategory getTitle={getTitle} ProductsData={RandomData}
                                                            title={" "}/>}/>
                        <Route path="for-childrens"
                               element={<CategorySection getTitle={getTitle} title={" "}/>}/>
                        <Route path="for-kitchen" element={<CategorySection getTitle={getTitle} title={" "}/>}/>
                        <Route path="sheets" element={<CategorySection getTitle={getTitle} title={""}/>}/>
                        <Route path="bedspreads" element={<CategorySection getTitle={getTitle} title={""}/>}/>
                        <Route path="novelties" element={<CategorySection getTitle={getTitle} title={""}/>}/>
                    </Routes>
                </div>
            </div>
        </div>
    )
}

export default Categories;